.container.testimonials-container{
width: 40%;
padding-bottom: 4rem;
}

.client__avatar img{
    
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    overflow: hidden;
    border: 0.4rem solid var(--color-primary-variant);
    margin: 0 auto 1rem;
}

.testimonial{
    text-align: center;
    background: var(--color-bg-variant);
    padding: 2rem;
    border-radius: 2rem;
    user-select: none;
}

.client__review{

    color: var(--colo-light);
    font-weight: 300;
    width: 80%;
    display: block;
    margin: 0.8rem auto 0;
}



/*=======MEDIA QUERRIES (MEDIUN DEVICES) ===*/
@media screen and (max-width:1024px) {
    .container.testimonials-container{
        width: 60%;
        }
  
    }
    
    /*=======MEDIA QUERRIES (SMALL DEVICES) ===*/
    
    @media screen and (max-width:600px) {
    
        .container.testimonials-container{
            width:var(--container-width-sm);
            }
            .client__review{
                width:var(--container-width-sm) ;
            }
     
    }