header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}


/* ==== CTA ===*/

.cta{
    position: relative;
    display: flex;
    justify-content: center;
    margin-top: 1.5rem;
    gap: 1.1rem;
}

/* ==== HEADER SOCIALS ===*/

.header__socials{
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    left:0;
    bottom:3rem;
}
a{
    color: var(--color-primary);
}


/* ==== ME ===*/

.me img{
width: 12rem;
height: 18rem;
 margin-top: 4rem;
 border-radius: 12rem 12rem 0 0;
 position: absolute;
 left: calc(50% - 6rem);
 overflow: hidden;
}

/* ==== SCROLL DOWN ===*/

.scroll__down{
position: absolute;
transform: rotate(90deg);
right: -2.3rem;
bottom:5rem;
font-weight: 300;
font-size: 0.9rem;
}


/*=======MEDIA QUERRIES (MEDIUM DEVICES) ===*/

@media screen and (max-width:1024px) {
    header{
        height: 70vh;
    }
}

/*=======MEDIA QUERRIES (SMALL DEVICES) ===*/

@media screen and (max-width:600px) {
    header{
        height: 80vh;
    }
    .header__socials, .scroll__down{
        display: none;
    }
}