.about__container{
    padding-top: 4rem;
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me{
    width: 100%;
    height: 36%;
    background: linear-gradient(45deg,
    transparent,
    var(--color-primary), transparent)
    ;
    border-radius: 2rem;
    aspect-ratio: 1/1;
    display: grid;
    place-items: center;
    transition: var(--transition);

}

.about__me-image{
border-radius: 2rem;
transform: rotate(10deg);
overflow: hidden;
}
.about__me-image:hover{
    transform: rotate(0); 
   
}

.about__cards{
    display: grid;grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card{
    background: var(--color-bg-variant);
    border-radius: 2rem;
    border: 1px solid transparent;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about__card:hover{
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
}
.about__icon{
    font-size: 1.4rem;
    margin-bottom: 1rem;
    color: var(--color-primary);
}
.about__card h5{
    font-size: 0.96rem;
}
.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}
.about__content p{
    color: var(--color-light);
    margin: 2rem 0 2.6rem;
}

/*=======MEDIA QUERRIES (MEDIUN DEVICES) ===*/
@media screen and (max-width:1024px) {
  
.about__container{
    grid-template-columns: 1fr;
    gap: 0;
}
.about__me{
    width: 50%;
    height: 55%;
    margin: 1rem auto 2rem;
}

.about__content p{
    margin: 1rem 0 1.5rem;
}

}

/*=======MEDIA QUERRIES (SMALL DEVICES) ===*/

@media screen and (max-width:600px) {

    .about__me{
        width:65%;
        margin: 0 auto 2.5rem;
        
    }
    .about__cards{
    
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about__content{
        text-align: center;
    }
   
}





